import React from "react"
import styled from "styled-components"
import HelperBlock from "./helperBlock"

const WrapLayout = styled.div`
  color: #000;
  display: flex;
  & img {
    width: 80px;
    height: 80px;
    max-width: 50%;
    margin-right: 10px;
    border-radius: 10px;
  }
`

const UI = props => {
  const { config = {} } = props
  let newImage = config.Image || {}
  if (typeof newImage === "string") {
    newImage = JSON.parse(newImage)
  }
  const styleImage = HelperBlock.convertStyleMobileToWeb(newImage.Style)
  const styleText = HelperBlock.convertStyleMobileToWeb(config.Style)
  return (
    <WrapLayout>
      <img src={newImage?.Content} style={styleImage} alt="img" />
      <div style={styleText}>{config.Content}</div>
    </WrapLayout>
  )
}

export default UI
