import React from "react"
import styled from "styled-components"
import HelperBlock from "./helperBlock"

const WrapLayout = styled.div`
  color: #000;
  & img {
    width: 100%;
    height: auto;
    object-fit: cover;
    border-radius: 10px;
  }
`

const UI = props => {
  const { config = {} } = props
  const style = HelperBlock.convertStyleMobileToWeb(config.Style)
  return (
    <WrapLayout>
      <img style={style} src={config.Content} alt="img" />
    </WrapLayout>
  )
}

export default UI
