import React from "react"
import { HUtils } from "src/core/helpers"
import styled from "styled-components"
import HelperBlock from "./helperBlock"

const WrapLayout = styled.div`
  color: #000;
  & p {
    margin-block-start: 1em;
    margin-block-end: 1em;
  }
`

const UI = props => {
  const { config = {} } = props
  const style = HelperBlock.convertStyleMobileToWeb(config.Style)
  return (
    <WrapLayout style={style}>
      <div dangerouslySetInnerHTML={{ __html: config.Content }}></div>
    </WrapLayout>
  )
}

export default UI
