import React from "react"
import styled from "styled-components"
import HelperBlock from "./helperBlock"

const WrapLayout = styled.div`
  color: #000;
`

const UI = props => {
  const { config = {} } = props

  let newStyle = config.Style || {}
  if (config.Style && typeof config.Style === "string") {
    newStyle = JSON.parse(config.Style)
  }
  newStyle = { ...newStyle, fontSize: 16, marginRight: 16 }
  const style = HelperBlock.convertStyleMobileToWeb(config.Style)
  return <WrapLayout style={style}>{config.Content}</WrapLayout>
}

export default UI
