import React from "react"
import styled from "styled-components"
import HelperBlock from "./helperBlock"

const WrapLayout = styled.div`
  color: #000;
`

const UI = props => {
  const { config = {} } = props

  const style = HelperBlock.convertStyleMobileToWeb(config.Style)
  return (
    <WrapLayout>
      <a href={config.Url} target={config.Target || "_blank"} style={style}>
        {config.Content}
      </a>
    </WrapLayout>
  )
}

export default UI
