import React from "react"
import classnames from "classnames"
import styled from "styled-components"
import TypeText from "./TypesBlock/text"
import TypeImageText from "./TypesBlock/image_text"
import TypeImage from "./TypesBlock/image"
import TypeHtml from "./TypesBlock/html"
import TypeLink from "./TypesBlock/link"
const WrapBlock = styled.div`
  padding: 0rem 0px;
`

const Types = {
  text: TypeText,
  image_text: TypeImageText,
  image: TypeImage,
  link: TypeLink,
  html: TypeHtml,
}

export default function MUI_Block_Mobile(props) {
  const { data } = props
  let newObject = data || []
  if (typeof newObject === "string") {
    newObject = JSON.parse(newObject)
  }

  return (
    <WrapBlock className="mui-block_mobile">
      {newObject &&
        newObject?.map((v, i) => {
          // console.log("v:",v)
          if (v.ObjType && Types[v.ObjType]) {
            const UI = Types[v.ObjType]
            return <UI key={i} fnList={props.fnList} config={v} />
          }
          return <div key={i}></div>
        })}
    </WrapBlock>
  )
}
