const RatitoTextRNToRJ = 16 / 14
const RatitoSize = 2

const HelperBlock = {
  convertStyleMobileToWeb: (style = {}) => {
    let newStyle = style || {}
    if (newStyle && typeof newStyle === "string") {
      newStyle = JSON.parse(newStyle)
    }
    let convertedStyle = {}
    for (let property in newStyle) {
      let value = newStyle[property]
      switch (property) {
        case "width":
          convertedStyle["width"] = value
          if (typeof newStyle["width"] === "number") {
            convertedStyle["width"] = value * RatitoSize
          }
          break
        case "height":
          convertedStyle["height"] = value
          if (typeof newStyle["height"] === "number") {
            convertedStyle["height"] = value * RatitoSize
          }
          break
        case "borderRadius":
          convertedStyle["borderRadius"] = value
          if (typeof newStyle["borderRadius"] === "number") {
            convertedStyle["borderRadius"] = value * RatitoSize
          }
          break
        case "resizeMode":
          convertedStyle["objectFit"] = value
          break
        case "fontSize":
          convertedStyle["fontSize"] = parseInt(value) * RatitoTextRNToRJ
          break
        default:
          convertedStyle[property] = value
          break
      }
    }
    if (!newStyle.fontSize) {
      convertedStyle["fontSize"] = 16
    }

    return convertedStyle
  },
}

export default HelperBlock
